/* STATIC STYLES */

.card1 {
  position: relative;
  overflow: hidden;
  background-color: var(--color-primary-darker);
  border-radius: 0.5rem;
  transition: 
    transform 0.4s cubic-bezier(0.18, 0.89, 0.32, 1.28),
    box-shadow 0.3s ease,
    background-color 0.3s ease;
}

.card1:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 
    0 12px 20px -10px var(--color-primary-darker),
    0 4px 20px 0 var(--color-primary-darker);
  background-color: color-mix(in srgb, var(--color-primary-darker), var(--color-secondary) 2%);
}

.spotlight-container {
  position: absolute;
  inset: 0;
}

.spotlight-container > img {
  width: 100%;
  height: 100%;
  object-fit: fill;
  border-radius: 0.5rem;
}

