/* STATIC STYLES */

.card2 {
  --start: 0;
  position: relative;
  overflow: hidden;
  border: 1px solid var(--color-primary-darkest);
  border-radius: 1rem;
  background-color: var(--color-primary-darker);
  transition: border-color 1s ease-in-out;
  break-inside: avoid-column;
}

.card2::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 1rem;
  border: 2px solid transparent;
  background: radial-gradient(circle, var(--color-primary-text) 0%, var(--color-primary) 100%);
  background-attachment: fixed;
  mask: linear-gradient(#0000, #0000),
    conic-gradient(
      from calc((var(--start) - 15) * 1deg),
      #ffffff1f 0deg,
      #fff,
      #ffffff00 100deg
    );
  mask-composite: intersect;
  mask-clip: padding-box, border-box;
  opacity: 0;
  transition: 0.5s ease;
}

.card2:hover::before {
  opacity: 1;
}

.card2-glow {
  pointer-events: none;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  filter: blur(10px) saturate(200%);
}

