/* STATIC STYLES */

@property --angle {
  syntax: "<angle>";
  initial-value: 0deg;
  inherits: false;
}

@keyframes spin {
  to {
    --angle: 360deg;
  }
}

.card3 {
  position: relative;
  overflow: hidden;
  border: none;
  border-radius: 1rem;
  backdrop-filter: blur(10px);
  background-color: var(--color-card);
  isolation: isolate;
  transition: 
    transform 0.4s cubic-bezier(0.18, 0.89, 0.32, 1.28),
    box-shadow 0.3s ease,
    background-color 0.3s ease;
}

.card3::before {
  content: '';
  position: absolute;
  z-index: -1;
  inset: 0;
  padding: 2px;
  border-radius: inherit;
  background: linear-gradient(45deg, var(--color-primary), var(--color-secondary), var(--color-primary));
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}

.card3::after {
  content: '';
  position: absolute;
  z-index: -1;
  inset: 0;
  padding: 2px;
  border-radius: inherit;
  opacity: 0;
  background: conic-gradient(from var(--angle), var(--color-primary), var(--color-secondary), var(--color-primary));
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  transition: opacity 0.3s ease;
}

.card3:hover {
  box-shadow: 
    0 12px 20px -10px var(--color-primary-darker),
    0 4px 20px 0 var(--color-primary-darker);
  background-color: color-mix(in srgb, var(--color-primary-darker), white 2%);
}

.card3:hover::after {
  opacity: 1;
  animation: spin 1s linear infinite;
}

