/* STATIC STYLES */

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  margin: 0 auto;
  padding: 1rem 1.5rem;
  z-index: 99999;
  background-color: transparent;
  transition: background-color 0.3s ease;
}

.navbar.scrolled {
  background-color: #050816;
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 1rem;
  text-decoration: none;
}

.logo {
  width: 2.5rem;
  object-fit: contain;
}

.logo-text {
  display: flex;
  color: var(--color-primary-text-dark);
  font-weight: 700;
  cursor: pointer;
}

.desktop-links {
  display: flex;
  flex-direction: row;
  gap: 1.5rem;
  list-style: none;
}

.desktop-link {
  font-size: 1.1em;
  font-weight: 500;
  cursor: pointer;
  color: var(--color-primary-text-dark);
  transition: color 0.3s ease;
}
.desktop-link:hover {
  color: var(--color-primary-text);
}
.desktop-link.active {
  color: var(--color-primary-text);
}

.desktop-link a {
  position: relative;
  text-decoration: none;
  color: inherit;
  place-items: center;
}

.underline {
  position: absolute;
  bottom: -0.5rem;

  /*left: 0;*/
  left: 50%;
  transform: translateX(-50%);

  width: 0;
  height: 0.125rem;
  background-color: var(--color-primary-text);
  transition: all 0.3s ease;
}

.desktop-link:hover .underline {
  width: 100%;
}
.desktop-link.active .underline {
  width: 100%;
}

.mobile-menu {
  display: none;
  flex: 1;
  justify-content: flex-end;
  align-items: center;
}

.menu-icon {
  width: 28px;
  height: 28px;
  object-fit: contain;
  cursor: pointer;
}

.mobile-links {
  display: none;
  position: absolute;
  top: 80px;
  right: 20px;
  min-width: 140px;
  z-index: 10;
  padding: 20px;
  border-radius: 10px;
  background: linear-gradient(140deg, #ffffff -300%, #6d6d6d -70%, #11101d 100%);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.7);
}

.mobile-links.show {
  display: flex;
}

.mobile-links ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}

.mobile-link-item {
  font-weight: 500;
  cursor: pointer;
  color: var(--color-primary-text-dark);
  transition: color 0.3s ease;
}
.mobile-link-item:hover {
  color: var(--color-primary-text);
}
.mobile-link-item.active {
  color: var(--color-primary-text);
}

.mobile-link-item a {
  text-decoration: none;
  color: inherit;
}

