/* STATIC STYLES */

#hero {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-block: 5rem;
}

.hero-background {
  position: absolute;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  opacity: 0.1;
}

.hero-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 10;
  width: 100%;
  height: 100%;
  padding: 0 10%;
}

.hero-text-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  max-width: 50%;
}

.hero-text-container h1 {
  color: var(--color-primary-text);
  line-height: 1.2;
}

.hero-text-container h3 {
  color: var(--color-primary-text-dark);
  line-height: 1.2;
}

.desk-container {
  flex: 1;
  max-width: 45%;
  height: 100%;
}

.sliding-line {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  line-height: 1.2;
}

.slide {
  display: inline-block;
  position: relative;
  height: 2.5rem;
  overflow: hidden;
}

.wrapper {
  display: flex;
  flex-direction: column;
  animation: wordSlider 6s infinite ease-in-out;
}

.sliding-word {
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  white-space: nowrap;
  padding-left: 0.2rem;
}

@keyframes wordSlider {
  0%, 25% {
    transform: translateY(0%);
  }
  35%, 65% {
    transform: translateY(-50%);
  }
  75%, 100% {
    transform: translateY(0%);
  }
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {

}

@media (max-width: 992px) {
  .hero-container {
    flex-direction: column;
    justify-content: center;
    padding: 8rem 2rem 0 2rem;
  }

  .hero-text-container {
    gap: 1rem;
    max-width: 100%;
    text-align: center;
    place-items: center;
  }
}

@media (max-width: 768px) {
  .desk-container {
    min-width: 100%;
  }
}

