/* STATIC STYLES */

.btn {
  position: relative;
  cursor: pointer;
  text-decoration: none;
  padding: .5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--color-primary-dark);
  border-radius: 0.5rem;
  overflow: hidden;
  transition: transform 0.3s ease;
}

.btn .circle {
  position: absolute;
  width: 120%;
  height: 120%;
  border-radius: 9999px;
  background-color: var(--color-secondary-dark);
  transition: all 0.5s ease;
  transform-origin: center;
}

.btn:hover .circle {
  width: 2.5rem;
  height: 2.5rem;
  right: 1rem;
}

.btn .text {
  font-weight: 600;
  color: var(--color-primary-darkest);
  transition: all 0.5s ease;
  transform: translateX(-0.5rem);
  z-index: 1;
}

.btn:hover .text {
  color: var(--color-primary-text-dark);
}

.btn .icon-wrapper {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 9999px;
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: all 0.5s ease;
  z-index: 1;
}

.btn:hover .icon-wrapper {
  animation: bounce 1.5s infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(-50%) translateY(0.15rem);
  }
  50% {
    transform: translateY(-50%) translateY(-0.25rem);
  }
}

