/* STATIC STYLES */

.about-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.about-cards > * {
  flex: 0 0 20rem;
}

.about-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1rem;
  padding: 2rem;
}

.technologies {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 1rem;
}

