/* STATIC STYLES */

.experience-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.developer-container {
  height: 100%;
}

.experience-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: start;
  gap: 2rem;
  padding: 5rem 0
}

.experience-cards > * {
  flex: 0 0 16rem;
}

.experience-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 2rem 3rem;
  cursor: pointer;
}

.experience-card-header {
  color: var(--color-primary-text-dark);
}

.experience-card-inner p {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {

}

@media (max-width: 992px) {
  .experience-content {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
  }
  
  .developer-container {
    flex: 1;
    min-width: 250px;
  }
  
  .experience-cards {
    flex: 2;
  }

  .experience-card {
    padding: 1rem 2rem;
  }
  
  .icon-card-content {
    padding: 1.2rem;
  }
}

@media (max-width: 768px) {
  .experience-icons > * {
    flex: 0 0 10rem;
  }
}

@media (max-width: 576px) {
  .icon-card-content {
    padding: 1rem;
  }
}