/* STATIC STYLES */

.portfolio-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

.portfolio-card-container {
  margin-top: 4rem;
}

.portfolio-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1rem;
  position: relative;
  padding: 2rem;
  z-index: 1000;
}

.portfolio-card-tags {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
}

.portfolio-card-tags img {
  width: 2rem;
}

.portfolio-navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin-top: 2rem;
}

.portfolio-arrow {
  background: var(--color-primary-darker);
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  color: var(--color-primary-text);
}

.portfolio-arrow:hover {
  background: var(--color-primary-dark);
  transform: scale(1.1);
}
.portfolio-arrow:active {
  transform: scale(0.95);
}

.portfolio-counter {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.1rem;
  color: var(--color-primary-text);
}
.current-index {
  font-weight: 700;
  color: var(--color-primary-text);
}
.total-count {
  opacity: 0.7;
}
.counter-divider {
  opacity: 0.5;
}

.laptop-container {
  height: 70vh;
  width: 50vw;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {

}

@media (max-width: 992px) {
  .laptop-container {
    height: 55vh;
  }
}

@media (max-width: 768px) {
  .portfolio-content {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .portfolio-card-container {
    margin-top: 0;
  }

  .laptop-container {
    width: 90vw;
    height: 65vh;
  }
}

@media (max-width: 576px) {
  .laptop-container {
    width: 90vw;
    height: 40vh;
  }
}